.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

body {
  background-color: #cccccc; 
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*html {*/
:root {
  --color-text: black;
  --color-background: #282c34;
  --color-primary: #c1ffd7;
  --color-seconday: #e1eedd;
  --color-info: #d1e8e2;
  --color-warning: #f0a967;
  --color-danger: #ff6961;
  --color-error: #ff6961;

  --toastify-toast-width: 35vw !important;
}


.genericButton {
  color: black;
  box-shadow: none !important;
}

.genericButton:hover, .genericButton:focus, .genericButton:disabled  {
  color: black;
  box-shadow: none !important;
}

.genericButton:active {
  color: black;
  box-shadow: none !important;
  filter: brightness(.9);
}


